.prv-card {
    &.prv-style-1 {
        position: relative;
        background: var(--color-white);
        border-radius: 16px;
        box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.16);
        overflow: hidden;
        height: 410px;
        max-width: 320px;
        margin: 0 auto;

        @media (min-width: 768px) {
            height: 450px;
        }

        @media (min-width: 576px) {
            max-width: 385px;
        }

        @media (min-width: 1024px) {
            display: flex;
            height: 550px;
            padding-bottom: 20px;
            flex-direction: column;
            align-items: center;
            flex-shrink: 0;
        }

        .prv-card {
            &__img-top {
                display: block;

                >img {
                    display: block;
                    width: auto;
                    height: auto;
                    max-width: 100%;
                    margin: 0 auto;
                }
            }

            &__body {
                display: flex;
                height: auto;
                padding: 18px 22px;
                flex-direction: column;
                justify-content: flex-start;
                align-items: flex-start;

                @media (min-width: 1024px) {
                    height: 336px;
                    padding: 0px 25px;
                    justify-content: center;
                }

                .card {
                    &__badge {
                        font-size: 14px;
                        font-weight: 700;
                        line-height: 1.2;
                        margin-bottom: 0;

                        @media (min-width: 992px) {
                            margin-bottom: 12px;
                        }
                    }

                    &__box {
                        position: relative;
                        overflow: hidden;
                        display: block;

                        @media (min-width: 1024px) {
                            height: 160px;
                        }
                    }

                    &__title {
                        font-size: 20px;
                        font-weight: 700;
                        font-family: var(--fontBanda);
                        line-height: 1.3;
                        margin-bottom: 0;

                        @media (min-width: 1024px) {
                            font-size: 24px;
