                    }

                    &__description {
                        font-size: 16px;
                        font-weight: 400;
                        line-height: 1.2;
                        margin-bottom: 0;

                        @media (min-width: 1024px) {
                            font-size: 18px;
                        }

                        >* {
                            font-size: 16px;
                            font-weight: 400;
                            line-height: 1.2;

                            @media (min-width: 1024px) {
                                font-size: 18px;
                            }

                            &:last-child {
                                margin-bottom: 0;
                            }
                        }
                    }
                    &__button {
                        margin-top: 12px;
                    }
                }
            }
        }
