.fieldset {
    padding: 0;
    margin: 0;
    border: 0;
    .fieldset-legend {
      display: none;
      margin: 0;
    }
  }
  .pvr__search {
    position: relative;
    .search {
      &__control{
          position: relative;
      }
      &__input {
        width: 100%;
        height: 40px;
        background: var(--color-white);
        padding: 8px 24px;
        border-radius: 24px;
        border: 1px solid var(--color-gray-60);
        outline: none;
        font-size: 16px;
        line-height: 20px;
        &[type="search"]::-webkit-search-decoration,
        &[type="search"]::-webkit-search-cancel-button,
        &[type="search"]::-webkit-search-results-button,
        &[type="search"]::-webkit-search-results-decoration {
          -webkit-appearance: none;
        }
        &::-webkit-input-placeholder {
          /* Chrome/Opera/Safari */
          opacity: 0;
        }
        &::-moz-placeholder {
          /* Firefox 19+ */
          opacity: 0;
        }
        &:-ms-input-placeholder {
          /* IE 10+ */
          opacity: 0;
        }
        &:-moz-placeholder {
          /* Firefox 18- */
          opacity: 0;
        }
        &:focus ~ .search__label,
        &:valid ~ .search__label {
          opacity: 0;
        }
      }
      &__label {
        opacity: 1;
        color: var(--color-white-80);
        font-family: var(--fontNunito);
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 20px;
        position: absolute;
        top: 50%;
        transform: translate(0, -50%);
        left: 24px;
        pointer-events: none;
      }
      &__btn {
        position: absolute;
        width: 24px;
        height: 24px;
        top: 50%;
        right: 15px;
        transform: translate(0, -50%);
        padding: 0;
        background: var(--color-white);
        border: none;
        cursor: pointer;
        @media (min-width: 1250px) {
          right: 24px;
        }
        .text {
          display: none;
        }
        .icon {
          color: var(--color-white-80);
          font-size: 24px;
        }
      }
      
    }
    .close {
      &__btn {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        display: flex;
        justify-content: center;
        align-items: center;
        width: 50px;
        height: 100%;
        background: transparent;
        border: 0;
        padding: 0;
        cursor: pointer;
        .text {
          display: inline-block;
          text-indent: -9999999px;
        }
        @media (min-width: 992px) {
          display: none;
          right: 0;
          left: inherit;
          padding-right: 15px;
        }
      }
    }
    .clear {
      &__btn {
        display: none;
        visibility: hidden;
        overflow: hidden;
        position: absolute;
        top: 50%;
        right: 24px;
        transform: translate(0, -50%);
        justify-content: center;
        align-items: center;
        width: 20px;
        height: 20px;
        background: transparent;
        padding: 0;
        border: 0;
        cursor: pointer;
        outline: none;
        .text {
          display: inline-block;
          text-indent: -9999999px;
        }
        @media (min-width: 992px) {
          display: flex;
        }
        &:before {
          position: absolute;
          content: "";
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
        }
      }
    }
    &--result {
      width: 100%;
      border-bottom: 1px solid var(--color-gray-60);
      .search {
        &__control {
          width: 100%;
          max-width: 808px;
          margin: 0 auto;
          position: relative;
        }
        &__input {
          width: 100%;
          height: 44px;
          background: var(--color-white);
          padding: 11px 24px 11px 76px;
          border: 0;
          outline: none;
          font-size: 16px;
          line-height: 20px;
          &[type="search"]::-webkit-search-decoration,
          &[type="search"]::-webkit-search-cancel-button,
          &[type="search"]::-webkit-search-results-button,
          &[type="search"]::-webkit-search-results-decoration {
            -webkit-appearance: none;
          }
          &::-webkit-input-placeholder {
            /* Chrome/Opera/Safari */
            opacity: 0;
          }
          &::-moz-placeholder {
            /* Firefox 19+ */
            opacity: 0;
          }
          &:-ms-input-placeholder {
            /* IE 10+ */
            opacity: 0;
          }
          &:-moz-placeholder {
            /* Firefox 18- */
            opacity: 0;
          }
          &:focus ~ .search__label,
          &:valid ~ .search__label {
            opacity: 0;
          }
        }
        &__label {
          opacity: 1;
          color: var(--color-gray-600);
          font-family: var(--fontNunito);
          font-size: 16px;
          font-style: normal;
          font-weight: 400;
          line-height: 20px;
          position: absolute;
          top: 50%;
          transform: translate(0, -50%);
          left: 76px;
          pointer-events: none;
        }
        &__btn {
          position: absolute;
          width: 24px;
          height: 24px;
          top: 50%;
          right: 15px;
          transform: translate(0, -50%);
          padding: 0;
          background: var(--color-white);
          border: none;
          cursor: pointer;
          @media (min-width: 1250px) {
            right: 24px;
          }
          .text {
            display: none;
          }
          .icon {
            color: var(--color-white-80);
            font-size: 24px;
          }
        }
      }
      .back {
        &__btn {
          position: absolute;
          top: 50%;
          left: 0;
          transform: translate(0, -50%);
          display: flex;
          justify-content: center;
          align-items: center;
          width: 44px;
          height: 100%;
          background: transparent;
          border-top:0;
          border-left:0;
          border-bottom: 0;
          border-right: 1px solid var(--color-gray-60);
          padding: 0;
          cursor: pointer;
          .text {
            display: inline-block;
            text-indent: -9999999px;
          }
        }
      }
      .clear {
        &__btn {
          display: none;
          visibility: hidden;
          overflow: hidden;
          position: absolute;
          top: 50%;
          right: 24px;
          transform: translate(0, -50%);
          justify-content: center;
          align-items: center;
          width: 20px;
          height: 20px;
          background: transparent;
          padding: 0;
          border: 0;
          cursor: pointer;
          // outline: none;
          .text {
            display: inline-block;
            text-indent: -9999999px;
          }
          @media (min-width: 992px) {
            display: flex;
          }
          &:before {
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
          }
        }
      }
    }  
  }
  